::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: "Space Grotesk", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  background-image: url("/public/assets/images/backgroundPortfolio.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

body a {
  text-decoration: none;
  color: unset;
}

body ul,
body li {
  list-style: none;
}

body main {
  max-width: 100vw;
  min-height: 90vh;
  display: flex;
  align-items: center;
  flex-direction: row;
  overflow: hidden;
}

body section {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100%;
}

body h2 {
  color: white;
  font-size: 36px;
  border-radius: 5px;
  top: 0;
  left: 70px;
  z-index: 2;
}

.slider-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slider-track {
  display: flex;
  width: 300%;
  /* 100% * nombre de slides */
  height: 100%;
  transition: transform 0.5s ease-in-out;
}

.slide {
  width: 100%;
  flex-shrink: 0;
}

.slider-pagination {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  width: 100%;
  bottom: 0;
}

.slider-pagination input {
  appearance: none;
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-radius: 50%;
  background-color: white;
  transition: background-color 0.3s ease, border 0.3s ease;
}

.slider-pagination :checked {
  background-color: orange;
}

.slider-pagination :hover {
  transform: scale(1.2);
  cursor: pointer;
}

.slider-track:hover ~ .slider-pagination {
  opacity: 0.2;
  transition: opacity 0.4s ease-in-out;
}

.prev, .next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 4rem;
  z-index: 3;
  transition: color 0.8s ease, font-size 0.8s ease;
  /* Correction ici */
}

.prev:hover, .next:hover {
  font-size: 6rem;
  color: orange;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

@media screen and (max-width: 1360px) {
  .main-cards .cards-container figure {
    max-height: 450px;
    min-height: 450px;
    width: 300px;
  }
}

@media screen and (max-width: 1024px) {
  .main-cards {
    flex-direction: column;
  }
  .main-cards .cards-container {
    display: flex;
    padding: 50px 0;
    align-items: center;
  }
  .main-cards .cards-container figure {
    width: 80%;
    max-width: 300px;
  }
  .skill-section .skill-container {
    width: 280px;
    height: 280px;
  }
  .skill-section .skill-container .grid-item {
    width: 90px;
    height: 90px;
    font-size: 32px;
  }
  .contact-section form {
    width: 85%;
    padding: 20px;
  }
  header nav ul {
    gap: 20px;
  }
  header nav ul li {
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  header {
    flex-direction: column;
    height: auto;
  }
  header .logo-container h1 {
    font-size: 18px;
  }
  header nav ul {
    flex-direction: row;
    align-items: center;
    gap: 10px;
  }
  .main {
    height: 100%;
  }
  .main h2 {
    font-size: 36px;
  }
  .main-cards h2 {
    font-size: 32px;
  }
  .main-cards .cards-container {
    display: flex;
    padding: 50px 0;
  }
  .main-cards .cards-container figure {
    width: 90%;
    max-width: 280px;
    min-height: auto;
  }
  .main-cards .contact-section h2 {
    font-size: 32px;
  }
  .main-cards .contact-section form {
    width: 90%;
    max-width: 400px;
  }
  .main-cards .contact-section form button {
    font-size: 16px;
  }
  .main-cards .skill-section .skill-container {
    width: 250px;
    height: 250px;
  }
  .main-cards .skill-section .skill-container .grid-item {
    width: 80px;
    height: 80px;
    font-size: 28px;
  }
}

@media screen and (max-width: 480px) {
  body .prev, body .next {
    position: absolute;
    top: 90%;
    transform: translateY(-50%);
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 3rem;
    z-index: 3;
    transition: color 0.8s ease, font-size 0.8s ease;
    /* Correction ici */
  }
  body h2 {
    font-size: 28px;
  }
  header .logo-container h1 {
    font-size: 16px;
  }
  header nav ul {
    gap: 5px;
  }
  header nav ul li {
    font-size: 12px;
  }
  .main-cards .cards-container {
    padding: 30px 0;
  }
  .main-cards .cards-container figure {
    width: 100%;
    max-width: 200px;
  }
  .main-cards .cards-container figure figcaption h3 {
    font-size: 20px;
  }
  .main-cards .cards-container figure figcaption p {
    font-size: 16px;
  }
  .contact-section form {
    padding: 15px;
  }
  .contact-section form button {
    width: 120px;
    font-size: 14px;
    padding: 10px;
  }
  .skill-section h2 {
    font-size: 28px;
  }
  .skill-section .skill-container {
    width: 220px;
    height: 220px;
  }
  .skill-section .skill-container .grid-item {
    width: 70px;
    height: 70px;
    font-size: 24px;
  }
}
