.main-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.main-cards h2 {
  font-size: 48px;
}

.main-cards .cards-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  box-sizing: content-box;
  width: 100%;
  max-width: 1200px;
}

.main-cards .cards-container .switch {
  position: relative;
  width: 7rem;
  height: 100px;
  top: -0.5rem;
  right: 10rem;
  opacity: 0.2;
  z-index: 1;
  font-size: 16px;
  border-radius: 25% 25% 0 0;
  text-align: center;
  cursor: pointer;
  background: linear-gradient(rgba(255, 255, 251, 0.635), rgba(255, 255, 255, 0));
  transition: opacity 0.8s ease, transform 0.8s ease, background-color 0.8s ease;
}

.main-cards .cards-container .switch:hover, .main-cards .cards-container .switch:focus {
  opacity: 0.6;
  transform: scale(1.1) translateY(-1rem);
  background: linear-gradient(orange, rgba(255, 255, 255, 0));
}

.main-cards .cards-container figure {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  max-height: 500px;
  min-height: 500px;
  width: 350px;
  padding: 20px;
  z-index: 2;
  border-radius: 8px;
  color: white;
  font-size: 20px;
  background: linear-gradient(rgba(180, 115, 246, 0.804), rgba(209, 149, 36, 0.781));
  box-shadow: 10px 8px 5px 10px rgba(0, 0, 0, 0.626);
  transition: transform 0.8s ease, background-color 2s ease, max-height 0.8s ease;
}

.main-cards .cards-container figure figcaption h3 {
  margin: 5px 0 20px 0;
}

.main-cards .cards-container figure figcaption p {
  transition: font-size 0.8s ease;
}

.main-cards .cards-container figure .wrapper .triangle {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 0;
  border-left: 25rem solid transparent;
  border-bottom: 22rem solid rgba(23, 30, 36, 0.9);
  transition: transform 1s ease, opacity 2s ease;
  z-index: 2;
}

.main-cards .cards-container figure .wrapper img {
  opacity: 0;
  width: 100%;
  height: 200px;
  border-radius: 5px;
  transition: opacity 0.8s ease;
  z-index: 1;
  border: 2px solid rgba(23, 30, 36, 0.9);
}

.main-cards .cards-container figure .wrapper .skill-card {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 10px;
  top: 4rem;
  opacity: 0;
  transition: opacity 0.8s ease, transform 0.8s ease;
  margin: 0;
  padding: 0;
  height: 100%;
}

.main-cards .cards-container figure .wrapper .skill-card li {
  margin: 0;
  padding: 2px;
  border-radius: 5px;
  color: white;
  background-color: rgba(23, 30, 36, 0.9);
}

.main-cards .cards-container figure:hover {
  max-height: 650px;
  transform: scale(1.05);
  background-color: black;
}

.main-cards .cards-container figure:hover figcaption p {
  font-size: 0;
}

.main-cards .cards-container figure:hover .triangle {
  transition: transform 2s ease, opacity 2s ease;
  transform: translateX(400px);
  opacity: 0;
}

.main-cards .cards-container figure:hover img,
.main-cards .cards-container figure:hover .skill-card {
  transition: opacity 0.8s ease;
  opacity: 1;
}

.main-cards .grid {
  display: grid;
}

.main-cards .btn-box {
  position: absolute;
  bottom: 5px;
}

.main-cards .btn {
  background-color: #4a90e2;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.main-cards .btn:disabled {
  display: none;
}
